import { AbstractLink, HyperLinkParameter, HyperLinkProps } from './abstract.link';
import { openUrl } from '../hyper_link.service';

export class ParameterLink extends AbstractLink {
    protected parameters: HyperLinkParameter[];
    public constructor(props: ParameterLinkProps) {
        super(props);
        const { parameters } = props;
        this.parameters = parameters;
    }
    protected _excute() {
        openUrl({
            target: '_self',
            url: this.url,
        });
    }

    protected get url(): string {
        const url = new URL(window.location.href);
        BI.each(this.parameters, (_index: number, parameter: HyperLinkParameter) => {
            if (url.searchParams.has(parameter.name)) {
                url.searchParams.set(parameter.name, parameter.value.toString());
            } else {
                url.searchParams.append(parameter.name, parameter.value.toString());
            }
        });
        
        return url.href;
    }
}

export interface ParameterLinkProps extends HyperLinkProps {
    parameters: HyperLinkParameter[];
}
